import React from 'react';
import { ResultsCount } from '../ResultsCount/ResultsCount';

interface Props {
    totalResults: string;
}

export const Navbar: React.FC<Props> = ({ totalResults }) => {
    return (
        <nav
            className="navbar fixed-top"
            style={{
                background: "rgba(255, 255, 255, 0.19)",
                borderRadius: "0",
                boxShadow: "0 4px 30px rgba(0, 0, 0, 0.1)",
                backdropFilter: "blur(5px)",
                WebkitBackdropFilter: "blur(15px)",
            }}
        >
            <div className="container-fluid row" style={{ justifyContent: "center", marginTop: "1rem", marginLeft: "0.1rem" }}>
                <img style={{ width: "21rem" }} src="https://raw.githubusercontent.com/stefanosallemi/TMDB-APP/main/src/components/Navbar/tmdb.png" alt="tmdb-logo" />
                <div className='d-flex justify-content-center'>
                    <ResultsCount total_results={totalResults} />
                </div>
            </div>
        </nav>
    );
};
